import { Container, TableCell, TableBody, Paper, Table, TableContainer, TableHead, TableRow, Typography } from "@material-ui/core"
import axios from 'axios'
import { useEffect } from "react"
import { useState } from "react"

const pisteJako = [
    10, 6, 4, 3, 2, 1
]

const PointsList = () => {

    const [pisteet, setPisteet] = useState([])
    useEffect(() => {
        axios.get('https://risbeegomfkerho.herokuapp.com/api/games').then(res => {
            const taulu = []
            for (const kisa of res.data) {
                for (const player of kisa.players) {
                    let playerObj = taulu.find(p => p.name === player.name)
                    if (!playerObj || playerObj === null) {
                        playerObj = { name: player.name, pisteet: 0 }
                        taulu.push(playerObj)
                    }
                    playerObj.pisteet += pisteJako[player.rankHC - 1] || 0
                    if (player.rank === 1) playerObj.pisteet++
                }
            }
            setPisteet(taulu.sort((a, b) => b.pisteet - a.pisteet))
        })
    }, [])
    console.log(pisteet)
    return (
        <Container>
            <Typography variant="h4" gutterBottom>Pistetaulukko</Typography>
            <TableContainer component={Paper} style={{ borderRadius: 10, marginBottom: 30, maxWidth: 500 }}>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>Sija</TableCell>
                            <TableCell>Pelaaja</TableCell>
                            <TableCell>Pisteet</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {pisteet.map((p, i) => <PelaajanPisteet i={i} pelaaja={p} key={p.name} />)}
                    </TableBody>
                </Table>
            </TableContainer>
            <Typography variant="h5">Pisteiden jako</Typography>
            <TableContainer component={Paper} style={{ borderRadius: 20, maxWidth: 500 }}>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>Sija</TableCell>
                            <TableCell>Pisteet</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {pisteJako.map((p,i) => <TableRow hover="true"><TableCell>{i+1}.</TableCell><TableCell>{p}</TableCell></TableRow>)}
                        <TableRow><TableCell>Nopein kierros</TableCell><TableCell>1</TableCell></TableRow>
                    </TableBody>
                </Table>
            </TableContainer>
        </Container>
    )
}
const PelaajanPisteet = ({ pelaaja, i }) => {
    return (
        <TableRow hover="true">
            <TableCell>{i+1}.</TableCell>
            <TableCell>{pelaaja.name}</TableCell>
            <TableCell>{pelaaja.pisteet}</TableCell>

        </TableRow>
    )
}
export default PointsList